  --primary-color: rgb(17, 86, 102);
  --primary-color-darker: rgb(9, 48, 56);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: 'Open sans', sans-serif;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  max-width: 640px;
  margin: 50px auto;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
}


.main-title{
  font-weight: 100;
  color: var(--primary-color);
}


form{
  font-size: 30px;
  width: fit-content;
  margin-bottom: 45px;
  gap: 55px;
}


label, input{
  display: flex;
  flex-direction: row;
}


input[type="number"]{
  display: flex;
  flex-direction: row;
  width: 120px;
  height: 25px;
  text-align: center;
}

input[type="checkbox"]{
  width: 25px;
  height: 45px;
}


input[type="number"]:focus{
  outline: var(--primary-color);
  border: 2px solid var(--primary-color);
}

#submit-form{
  background-color: var(--primary-color);
  width: 150px;
  height: 50px;
  color: #fff;
  font-size: 15px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border: var(--primary-color);
}

#reset{
  background-color: #ffffff;
  width: 150px;
  height: 50px;
  color: var(--primary-color);
  font-size: 15px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border: 2px solid var(--primary-color);
}


#submit-form, #reset:hover{
  background-color: var(--primary-color-darker);
  color: #ffffff;
}


.amount-div, .add-div, .upper-div, .lower-div, .special-div{
  display: flex;
  flex-direction: row;
  gap: 15px;
  height: 45px;
}


.password-field{
  display: flex;
  flex-direction: row;
  gap: 15px;
}

#field{
  border: solid 2px var(--primary-color);
  border-radius: 2px;
  width: 85%;
  height: 50px;
  cursor: text;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 25px;
  overflow: hidden;
}



.icon{
  width: 45px;
  height: 45px;

}


.copy-button{
  background-color: none;
  border: none;
  outline: none;
  cursor: pointer;
  width: 45px;
  height: 45px;
  padding: 0;
}

.copy-button{
  outline: none;
}

.password{
  font-size: 35px;
  margin: 0;
  padding: 0;
  height: 100%;
}

